import I18n from 'i18n-js';

/* eslint @typescript-eslint/camelcase:0 */
import {en_US} from './locales/en_US';
import {zh_CN} from './locales/zh_CN';

import {LangPack} from "./typing";
import {getLocaleFromLocalStorage, setLocale} from "./localeHelper";

I18n.fallbacks = true;

I18n.translations = {
    en: en_US,
    zh: zh_CN,
};

setLocale(getLocaleFromLocalStorage());

export function getLang(): LangPack {
    return new Proxy(en_US, {
        get(obj, prop) {
            return I18n.t(prop as string) || (obj as any)[prop];
        },
    }) as LangPack;
}
