import * as React from 'react';
import _ from 'lodash';

/* eslint @typescript-eslint/indent:0 */
type RC<P> = React.SFC<P>
    | React.ComponentClass<P>
    | React.ComponentType<P>
    | React.FunctionComponent<P>
    | ((props: P) => React.ReactElement);

type HOC<O, P> = (C: RC<O>) => RC<P>;

// compose components from left to right
// const compose = <P>(C: RC<P>, ...hocs: HOC<any, any>[]): RC<P> => hocs.reduce((g, f) => f(g), C);

type AnnotationTypes = HOC<any, any>[];
type ComposeHocFns<P> = (c: RC<P>) => RC<P>;

const filterNotFunction = (fns: any) => _.filter(fns, f => typeof f === 'function');

export const compose = <P>(
    ...hocs: AnnotationTypes
): ComposeHocFns<P> => (C: React.ComponentType<P>) => (
    _.reverse(
        filterNotFunction(hocs)
    ).reduce((g, f) => f(g), C)
);
