import React, {CSSProperties} from "react";

type ViewProps<T> = {
    children?: any;
    style?: CSSProperties;
};

export function View({ children, style = {} }: ViewProps<JSX.Element>) {
    return (
        <div style={style}>
            {children}
        </div>
    );
}

export function Str({ children }: ViewProps<JSX.Element>) {
    return (
        <span>
            {children}
        </span>
    );
}

export function CardTitle({ children }: any) {
    return (
        <View>
            <Str>{children}</Str>
        </View>
    );
}
